"use client";

import { useEffect, useState } from "react";
import dynamic from "next/dynamic";
import { createClient } from "@supabase/supabase-js";

const Globe = dynamic(() => import("react-globe.gl"), { ssr: false });

const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL!,
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
);

type IntelItem = {
  id: number;
  headline: string;
  summary: string;
  region: string;
  sentiment_level: string | null;
  impact_score: number;
  confidence_score?: number;
  insights?: string | null;
  media_url?: string | null;
  is_anonymous?: boolean;
  created_at: string;
};

type GlowPoint = {
  lat: number;
  lng: number;
  color: string;
  label: string;
  intensity: number;
};

export default function Page() {
  const [intelFeed, setIntelFeed] = useState<IntelItem[]>([]);
  const [glows, setGlows] = useState<GlowPoint[]>([]);
  const [status, setStatus] = useState("üõ∞Ô∏è Connecting...");
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [form, setForm] = useState({
    headline: "",
    summary: "",
    region: "",
    sentiment_level: "Neutral",
    impact_score: 50,
    file: null as File | null,
    anonymous: false,
  });

  const regionMap: Record<string, [number, number]> = {
    "United States": [38, -97],
    China: [35, 105],
    India: [20, 78],
    Russia: [60, 90],
    Brazil: [-10, -55],
    Germany: [51, 10],
    France: [46, 2],
    Mexico: [23, -102],
    Canada: [56, -106],
    Australia: [-25, 133],
  };

  // üîÑ Load intel feed
  useEffect(() => {
    const loadIntel = async () => {
      const { data, error } = await supabase
        .from("intel_feed")
        .select("*")
        .order("created_at", { ascending: false })
        .limit(10);
      if (error) setStatus("‚ùå Error loading feed");
      else setIntelFeed(data || []);
    };
    loadIntel();
  }, []);

  // üåç Trigger globe glow pulse
  const triggerGlow = (region: string, confidence: number) => {
    const coords = regionMap[region];
    if (!coords) return;

    const color =
      confidence > 75 ? "gold" : confidence > 50 ? "yellow" : confidence > 25 ? "orange" : "red";

    const newGlow = {
      lat: coords[0],
      lng: coords[1],
      color,
      label: region,
      intensity: confidence / 100,
    };
    setGlows((prev) => [newGlow, ...prev].slice(0, 6));
    setTimeout(() => setGlows((prev) => prev.filter((g) => g !== newGlow)), 8000);
  };

  // üß† Submit intel to A.I.V.E.
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsAnalyzing(true);
    setStatus("üß† A.I.V.E. analyzing intel...");

    try {
      let uploadedUrl = null;

      // Upload file to Supabase Storage
      if (form.file) {
        const fileName = `${Date.now()}_${form.file.name}`;
        const { data, error: uploadError } = await supabase.storage
          .from("aive-uploads")
          .upload(fileName, form.file);
        if (uploadError) throw uploadError;
        const { data: urlData } = supabase.storage.from("aive-uploads").getPublicUrl(fileName);
        uploadedUrl = urlData.publicUrl;
      }

      // Send text for AI analysis
      const res = await fetch(process.env.NEXT_PUBLIC_AIVE_FUNCTION_URL!, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          headline: form.headline,
          summary: form.summary,
          region: form.region,
          sentiment: form.sentiment_level,
          impact: form.impact_score,
        }),
      });

      const data = await res.json();
      const confidence = data.confidence ?? 50;
      const insight = data.insights ?? "No insight generated.";

      // Insert record into Supabase
      const { error } = await supabase.from("intel_feed").insert([
        {
          headline: form.headline,
          summary: form.summary,
          region: form.region,
          sentiment_level: form.sentiment_level,
          impact_score: form.impact_score,
          confidence_score: confidence,
          insights: insight,
          media_url: uploadedUrl,
          is_anonymous: form.anonymous,
        },
      ]);
      if (error) throw error;

      // Update UI and glow
      triggerGlow(form.region, confidence);
      setIntelFeed((prev) => [
        {
          id: Math.random(),
          headline: form.headline,
          summary: form.summary,
          region: form.region,
          sentiment_level: form.sentiment_level,
          impact_score: form.impact_score,
          confidence_score: confidence,
          insights: insight,
          media_url: uploadedUrl,
          is_anonymous: form.anonymous,
          created_at: new Date().toISOString(),
        },
        ...prev,
      ]);

      setStatus(`‚úÖ A.I.V.E. confidence ${confidence}%`);
    } catch (err) {
      console.error("Error:", err);
      setStatus("‚ùå A.I.V.E. failed to analyze intel");
    } finally {
      setIsAnalyzing(false);
    }
  };

  return (
    <main className="min-h-screen bg-gradient-to-br from-slate-900 via-gray-800 to-black text-white font-mono flex flex-col lg:flex-row p-6">
      {/* LEFT COLUMN */}
      <div className="w-full lg:w-1/2 max-w-xl mx-auto space-y-4">
        <h1 className="text-3xl font-bold text-blue-400 text-center mb-1">
          eX Intelligence Command Center
        </h1>
        <p className="text-sm text-gray-400 text-center">{status}</p>

        {intelFeed.map((item) => (
          <div
            key={item.id}
            className="bg-slate-800/60 backdrop-blur-md border border-slate-700 p-4 rounded-xl shadow-md animate-fade-in"
          >
            <h2 className="text-lg font-semibold text-blue-300">{item.headline}</h2>
            <p className="text-gray-400 text-sm mt-1">{item.summary}</p>
            <div className="flex justify-between text-xs text-gray-400 mt-2">
              <span>üåç {item.region}</span>
              <span>üß≠ {item.sentiment_level || "Unknown"}</span>
              <span>üí• Impact: {item.impact_score}</span>
              {item.confidence_score && (
                <span
                  className={`font-semibold ${
                    item.confidence_score > 75
                      ? "text-yellow-400"
                      : item.confidence_score > 50
                      ? "text-amber-300"
                      : "text-red-400"
                  }`}
                >
                  üß† {item.confidence_score}%
                </span>
              )}
            </div>

            {/* Show insights */}
            {item.insights && (
              <div className="mt-2 text-xs text-gray-300 italic border-t border-slate-700 pt-2">
                {item.insights}
              </div>
            )}

            {/* Media preview */}
            {item.media_url && (
              <div className="mt-3 border-t border-slate-700 pt-2">
                {item.media_url.match(/\.(jpg|jpeg|png|gif|webp)$/i) ? (
                  <img
                    src={item.media_url}
                    alt="Intel Attachment"
                    className="rounded-lg mt-2 border border-slate-700 max-h-48 object-contain"
                  />
                ) : item.media_url.match(/\.(mp3|wav|ogg|m4a)$/i) ? (
                  <audio controls className="mt-2 w-full">
                    <source src={item.media_url} />
                    Your browser does not support audio playback.
                  </audio>
                ) : (
                  <a
                    href={item.media_url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="text-blue-400 text-xs"
                  >
                    üîó View Attachment
                  </a>
                )}
              </div>
            )}

            {/* Anonymous label */}
            {item.is_anonymous && (
              <p className="text-xs text-gray-500 italic mt-2">üîí Anonymous submission</p>
            )}
          </div>
        ))}
      </div>

      {/* RIGHT COLUMN */}
      <div className="w-full lg:w-1/2 flex flex-col items-center">
        <div className="h-[500px] w-full flex justify-center">
          <Globe
            width={500}
            height={500}
            globeImageUrl="//unpkg.com/three-globe/example/img/earth-dark.jpg"
            backgroundColor="rgba(0,0,0,0)"
            pointsData={glows}
            pointLat={(d) => d.lat}
            pointLng={(d) => d.lng}
            pointColor={(d) => d.color}
            pointAltitude={(d) => 0.2 + d.intensity * 0.3}
            pointRadius={(d) => 0.4 + d.intensity * 0.6}
            pointLabel={(d) => `${d.label}: ${Math.round(d.intensity * 100)}%`}
          />
        </div>

        {/* SUBMISSION FORM */}
        <form
          onSubmit={handleSubmit}
          className="mt-8 bg-slate-800/80 border border-slate-700 rounded-lg p-4 w-full max-w-md space-y-3"
        >
          <h2 className="text-lg text-blue-300 font-semibold text-center">
            {isAnalyzing ? "üß† A.I.V.E. Thinking..." : "üöÄ Submit Intel"}
          </h2>

          <input
            type="text"
            placeholder="Headline"
            className="w-full p-2 rounded bg-slate-700 text-white"
            value={form.headline}
            onChange={(e) => setForm({ ...form, headline: e.target.value })}
            required
          />
          <textarea
            placeholder="Summary"
            className="w-full p-2 rounded bg-slate-700 text-white"
            rows={2}
            value={form.summary}
            onChange={(e) => setForm({ ...form, summary: e.target.value })}
            required
          />
          <input
            type="text"
            placeholder="Region (e.g., Canada)"
            className="w-full p-2 rounded bg-slate-700 text-white"
            value={form.region}
            onChange={(e) => setForm({ ...form, region: e.target.value })}
            required
          />

          {/* Attachments */}
          <div className="space-y-2">
            <label className="text-xs text-gray-400">Attach Image or Audio (optional)</label>
            <input
              type="file"
              accept="image/*,audio/*"
              onChange={(e) => setForm({ ...form, file: e.target.files?.[0] || null })}
              className="w-full text-xs text-gray-300"
            />
          </div>

          <div className="flex gap-2">
            <select
              className="w-1/2 p-2 rounded bg-slate-700 text-white"
              value={form.sentiment_level}
              onChange={(e) => setForm({ ...form, sentiment_level: e.target.value })}
            >
              <option value="Positive">Positive</option>
              <option value="Neutral">Neutral</option>
              <option value="Negative">Negative</option>
            </select>
            <input
              type="number"
              min={0}
              max={100}
              placeholder="Impact Score"
              className="w-1/2 p-2 rounded bg-slate-700 text-white"
              value={form.impact_score}
              onChange={(e) =>
                setForm({ ...form, impact_score: parseInt(e.target.value) || 0 })
              }
            />
          </div>

          {/* Anonymous submission */}
          <div className="flex items-center gap-2 text-xs text-gray-400">
            <input
              type="checkbox"
              id="anon"
              checked={form.anonymous}
              onChange={(e) => setForm({ ...form, anonymous: e.target.checked })}
            />
            <label htmlFor="anon">Submit anonymously (no identifiers stored)</label>
          </div>

          <button
            type="submit"
            disabled={isAnalyzing}
            className={`w-full py-2 rounded-lg font-semibold transition ${
              isAnalyzing
                ? "bg-amber-500/70 text-white animate-pulse"
                : "bg-blue-600 hover:bg-blue-700 text-white"
            }`}
          >
            {isAnalyzing ? "Analyzing..." : "Submit to A.I.V.E."}
          </button>
        </form>
      </div>

      <style jsx global>{`
        @keyframes fade-in {
          from {
            opacity: 0;
            transform: translateY(10px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        .animate-fade-in {
          animation: fade-in 0.5s ease-in-out;
        }
      `}</style>
    </main>
  );
}
